/* Featured Projects page styling */
.featured-projects {
    background-color: #000000; /* Lighter background color */
    color: #1d68f2; /* Dark text color for consistency */
    padding: 5% 10%;
    text-align: center;
  }
  
  .featured-projects h1 {
    font-size: 4rem; /* Slightly smaller to match the header */
    font-weight: 700;
    color: #1d68f2; /* Dark text color */
    margin-bottom: 2.5rem;
    font-family: 'Roboto', sans-serif; /* Consistent font family */
  }
  
  .projects-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); /* Adjusted grid for responsiveness */
    gap: 2rem;
    margin-top: 3rem;
  }
  
  .project-card {
    background-color: #1d68f2; /* Lighter background for card */
    padding: 2.5rem;
    border-radius: 15px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 6px 15px rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: left;
    height: 100%;
    min-height: 400px;
    font-family: 'Inter', sans-serif; /* Consistent font */
  }
  
  .project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  .project-info h2 {
    font-size: 2.2rem;
    color: #000000;
    margin: 0;
    font-weight: 700;
  }
  
  .project-info p {
    font-size: 1.3rem;
    color: #000000;
    margin: 1.5rem 0;
    line-height: 1.6;
    font-family: 'Inter', sans-serif;
  }
  
  .tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin: 1.5rem 0;
  }
  
  .tech {
    background-color: #e4e6e9;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    font-size: 1rem;
    color: #2b2d42;
    font-weight: 600;
    transition: background-color 0.3s ease;
  }
  
  .tech:hover {
    background-color: #d1d3d8;
  }
  
  .links {
    display: flex;
    gap: 1rem;
  }
  
  .project-link {
    color: #1e1e1e;
    font-size: 1.2rem;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .project-link:hover {
    color: #0056b3;
  }
  
  .project-link:focus {
    outline: none;
  }
  
  /* Responsive tweaks */
  @media (max-width: 768px) {
    .project-card {
      min-height: 350px;
    }
    .project-info p {
      font-size: 1rem;
    }
    .tech-stack {
      justify-content: center;
    }
  }
  